<template lang="html">
  <div class="person-center-info borderB clearfix">
    <div class="person-info-default" v-if="!isLogin">
      <div class="login-box" @click="$router.push('/login')">
        <img :src="logo" alt="">
        <span>登录</span>
      </div>
    </div>
    <div class="person-info-vip fl" v-if="isLogin">
      <div class="person-info-vip__img" @click="$router.push('/accountmanage')">
        <img :src="headerImg" />
        <i class="v0-icon v-icon"></i>
      </div>
      <span @click="$router.push('/vipfunction')">VIP特权功能</span>
    </div>
    <div class="person-info-online fl" v-if="isLogin">
      <p class="name" :style="{paddingTop: paddingT}" @click="outLine()" >
        <span class="realName fl">{{userInfo.userInfo.nickName|| '暂无名称'}}</span>
        <span v-if="!isPayment" class="status fl" :class="{'activeColor': this.onLine}">{{text}}</span>
        <img v-if="!isPayment" :src="icon" alt="" class="">
          <!-- <i class="yes__icon" :style="{top: yesTop}"></i>   | toFormatMobile-->
      </p>
      <p class="number"><i class="phone__icon"></i>{{userInfo.userInfo.mobile | toFormatMobile}}</p>
      <p class="sum" v-if="isPayment">广告费余额<span>¥ {{userInfo.adFeeCount}}</span></p>
    </div>
    <div v-if="!isPayment && isLogin" class="person-info-right fr" @click="$router.push('/accountmanage')"><i class="arrow__icon"></i></div>
  </div>
</template>

<script>
// import api from '@/api'
import utils from '@/utils/utils'
// import filter from '@/utils/filter'
import minutils from '@/utils/minUtils'
import yesIcon from '@/assets/img/personcenter/yes@2x.png'
import noIcon from '@/assets/img/personcenter/no@2x.png'
import defaultImg from '@/assets/img/personcenter/ph2@3x.png'
import logo from '@/assets/img/logo.png'

export default {
  props: {
    isPayment: {
      type: Boolean,
      default: false
    },
    paddingT: {
      type: String,
      default: '.2rem'
    },
    yesTop: {
      type: String,
      default: '.4rem'
    },
    userInfo: Object
  },
  data () {
    return {
      logo: logo,
      onLine: true
    }
  },
  mounted () {
    sessionStorage.setItem('onLine', this.onLine)
  },
  computed: {
    isLogin: function () {
      return utils.isLogin()
    },
    icon: function () {
      return this.onLine ? yesIcon : noIcon
    },
    text: function () {
      return this.onLine ? '在线' : '下线'
    },
    headerImg: function () {
      return utils.isLogin() && this.userInfo.userInfo.logo ? this.userInfo.userInfo.logo : defaultImg
    }
  },
  methods: {
    outLine () {
      var self = this
      if (this.onLine) {
        minutils.MessageBoxConfirm({
          message: '下线后，您的号码将不能被别人搜索出来？',
          title: '',
          options: {
            confirmButtonText: '确认下线',
            cancelButtonText: '保持上线'
          },
          success: (action) => {
            if (action) {
              self.onLine = false
              self.icon = noIcon
              self.text = '下线'
              sessionStorage.setItem('onLine', self.onLine)
            }
          },
          fail: () => {
            console.log(self.onLine)
          }
        })
      } else {
        minutils.MessageBoxConfirm({
          message: '上线后，您的号码获取更多展示！',
          title: '提示',
          options: {
            confirmButtonText: '确认上线',
            cancelButtonText: '保持下线'
          },
          success: (action) => {
            if (action) {
              self.onLine = true
              self.icon = yesIcon
              self.text = '在线'
              sessionStorage.setItem('onLine', self.onLine)
            }
          },
          fail: () => {
          }
        })
      }
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';

.person-center-info {
  width: 100%;
  height: 2.42rem;
  padding: 0 .08rem 0 .22rem;
  overflow: hidden;
  background: @whiteColor;
  .person-info-default {
    width: 100%;
    height: 100%;
    .login-box {
      width: 1.6rem;
      height: 100%;
      margin: 0 auto;
      img {
        width: 1.6rem;
        height: 1.6rem;
        vertical-align: top;
      }
      span {
        display: inline-block;
        width: 1.6rem;
        text-align: center;
        height: .68rem;
        line-height: .68rem;
        font-size: .32rem;
        border-radius: .05rem;
        background: @activeColor;
        color: @whiteColor;
      }
    }
  }
  .person-info-vip {
    width: 2.12rem;
    height: 100%;
    .person-info-vip__img {
      width: 1.2rem;
      height: 1.2rem;
      margin: .2rem;
      position: relative;
      .v-icon {
        width: .4rem;
        height: .4rem;
        position: absolute;
      }
      .v0-icon {
        right: -.22rem;
        top: .44rem;
        background: url(../../assets/img/level/v-0@2x.png) no-repeat;
        background-size: .4rem .4rem;
      }
      .v1-icon {
        right: -.22rem;
        top: .44rem;
        background: url(../../assets/img/level/v-1@2x.png) no-repeat;
        background-size: .4rem .4rem;
      }
      img {
        width: 1.2rem;
        height: 1.2rem;
        border-radius: 50%;
      }
    }
    span {
      font-size: .3rem;
      border-bottom: .01rem solid @specialColor;
      color: @specialColor;

    }
  }
  .person-info-online {
    width: 3rem;
    height: 100%;
    p {
      font-size: .34rem;
      line-height: .72rem;
      position: relative;
    }
    .name {
      width: 4rem;
      display: inline-block;
      white-space: nowrap;
      // overflow: hidden;
      padding-left: .1rem;
      .realName {
        display: inline-block;
        max-width: 3.2rem;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      .status {
        font-size: .3rem;
        margin-left: .1rem;
      }
      img {
        width: .3rem;
        height: .3rem;
        position: relative;
        left: .1rem;
        top: .04rem;
      }
    }
    .number {
      padding-left: .48rem;
      .phone__icon {
        top: .14rem;
        left: 0;
      }
    }
    .sum {
      width: 5rem;
      font-size: .3rem;
      line-height: .72rem;
      span {
        color: @redColor;
        margin-left: .2rem;
      }
    }
  }
  .person-info-right {
    width: 1rem;
    height: 100%;
    position: relative;
    .arrow__icon {
      right: 0;
      top: .63rem;
    }
  }
}
</style>



