<template lang="html">
  <div class="payment-list">
    <ul class="payment-list-line clearfix">
      <li v-for="item in adFeeList">
        <div class="payment-list-item fl" @click="disamountClick(item)">
          <span class="top">¥ {{item.amount}}</span>
          <span class="bottom">售价{{item.amountDiscount}}元</span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    adFeeList: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    disamountClick (item) {
      this.$emit('disamount-cash', item)
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.payment-list {
  padding: .15rem .12rem;
  background: @whiteColor;
  .payment-list-line {
    overflow: hidden;
    li {
      float: left;
      width: 33.3%;
      padding: 0 .1rem;
    }
    .payment-list-item {
      text-align: center;
      width: 100%;
      height: 1.2rem;
      margin: .15rem 0 .15rem .3rem;
      color: @activeColor;
      border: .01rem solid @activeColor;
      border-radius: .05rem;
      &:first-child {
        margin-left: 0;
      }
      span {
        display: block;
      }
      .top {
        font-size: .32rem;
        line-height: .32rem;
        padding-top: .2rem;
      }
      .bottom {
        font-size: .28rem;
        line-height: .28rem;
        padding-top: .15rem;
      }
    }
  }
}
</style>



