<template lang="html">
  <div class="circle-head">
    <div class="circle-head-left" @click="$router.go(-1)">
      <img src="../../assets/img/head/callback_icon@3x.png" alt="" v-if="backShow">
    </div>
    <div class="circle-head-cnt">
      <div class="tt">
        <span>{{title}}</span>
        <img src="../../assets/img/head/down@2x.png" alt="" v-if="centerShow">
      </div>
    </div>
    <div class="circle-head-right">
      <img :src="rightImg" v-if="rightShow" alt="" @click="$router.push('/messagewarn')">
    </div>
  </div>
</template>

<script>

export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    rightImg: {
      type: String
    },
    centerShow: {
      type: Boolean,
      default: true
    },
    rightShow: {
      type: Boolean,
      default: true
    },
    backShow: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      downShow: false
    }
  },
  methods: {
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
.circle-head {
  background: @whiteColor;
  height: .88rem;
  line-height: .88rem;
  border-bottom: 1px solid @lineColor;
  position: fixed;
  z-index: 1;
  padding: 0 .22rem;
  font-size: .32rem;
  color: @defaultColor;
  top: 0;
  left: 0;
  right: 0;
  img {
    width: .44rem;
    height: .44rem;
    vertical-align: middle;
  }
  .circle-head-left {
    float: left;
    width: 2rem;
    margin-right: -2rem;
    position: relative;
  }
  .circle-head-cnt {
    float: left;
    width: 100%;
    text-align: center;
    .tt {
      margin: 0 2rem;
      position: relative;
    }
  }
  .circle-head-right {
    float: right;
    width: 2rem;
    text-align: right;
    margin-left: -2rem;
  }
}
</style>



