<template lang="html">
  <div class="person-center-page">
    <person-center-head :rightShow="false" :centerShow="false" title="广告费充值"></person-center-head>
    <div class="" id="wrapper">
      <person-center-info :userInfo="this.userInfo"
      :isPayment="true" paddingT="0" yesTop=".2rem"></person-center-info>

      <p class="light-title">选择充值方式</p>
      <white-title margin="0 .22rem" padding="0" borderB="1px solid #ddd" text="广告费充值" :hasImg="icon[1]" text-color="#fc6703" :isShow="true" @click.native="toCommRateCash"></white-title>
      <white-title margin="0 .22rem" padding="0" borderB="1px solid #ddd" text="广告费兑换召币" text-color="#fc6703" :hasImg="icon[3]" :isShow="true" @click.native="$router.push('/adfeetozhaobi')"></white-title>
      <white-title margin="0 .22rem" padding="0" borderB="1px solid #ddd" text="召币兑换广告费" text-color="#fc6703" :hasImg="icon[0]" :isShow="true" @click.native="$router.push('/zhaobitoadfee')"></white-title>
      <white-title text="赠送召币" text-color="#fc6703" :hasImg="icon[4]" :isShow="true" @click.native="$router.push('/sendzhaobi')"></white-title>
      <!-- <p class="light-title">返点券</p>
      <white-title text="我的返点券" :hasImg="icon[2]" text-color="#fc6703" :isShow="true" @click.native="$router.push('/callnotes')"></white-title> -->
      <!-- <white-title text="大额充值优惠" class="borderB" marginT=".3rem" ></white-title> -->
      <div class="">
        <p class="light-title">大额充值优惠</p>
        <payment-list v-if="adFeeList.length" :adFeeList="adFeeList" @disamount-cash="toRateCash"></payment-list>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import personCenterHead from '@/components/head/personCenterHead'
import whiteTitle from '@/components/common/whiteTitle'
import personCenterInfo from '@/components/personCenter/personCenterInfo'
import paymentList from '@/components/payment/paymentList'
import zhao from '@/assets/img/payment/zhao@2x.png'
import xian from '@/assets/img/payment/xian@2x.png'
import juan from '@/assets/img/payment/juan@2x.png'
import dui from '@/assets/img/payment/dui@2x.png'
import song from '@/assets/img/payment/song@2x.png'

export default {
  components: { personCenterHead, personCenterInfo, whiteTitle, paymentList },
  data () {
    return {
      icon: [zhao, xian, juan, dui, song],
      userInfo: {
        'uncommentCount': 0,
        'illegalCount': 0,
        'badCommentCount': 0,
        'adFeeCount': 0,
        'zhaoBiCount': 0,
        'userInfo': {
          'mobile': '',
          'bankTag': 0,
          'realName': ''
        }
      },
      adFeeList: []
    }
  },
  created () {
    this.initPage()
  },
  computed: {
    userId () {
      return utils.userId()
    }
  },
  methods: {
    initPage () {
      this.ajaxGetUserInfo({userId: this.userId})
      this.ajaxGetRechargeAdFeeDiscount()
    },
    selectCircle (item) {
      this.title = item
    },
    toCommRateCash () {
      // if (this.userInfo.userInfo.bankTag === 0) {
      //   this.isBindCard()
      //   return
      // }
      this.$router.push({path: 'convertpoint', query: {type: 1}})
    },
    toRateCash (item) {
      // if (this.userInfo.userInfo.bankTag === 0) {
      //   this.isBindCard()
      //   return
      // }
      this.$router.push({
        path: 'convertpoint',
        query: {disamount: item.amountDiscount, cash: item.amount, type: 2, disamountId: item.id}
      })
    },
    isBindCard () {
      let self = this
      minutils.MessageBox({
        message: '您尚未绑定您的银行卡, 无法进行广告费充值',
        callback () {
          self.$router.push({path: 'bindbankcard', query: {realName: encodeURI(self.userInfo.userInfo.realName)}})
        }
      })
    },
    hasPayWord () {
      let self = this
      minutils.MessageBox({
        message: '您暂未设置支付密码，请设置支付密码后充值！',
        callback () {
          self.$router.push('modifypayword')
        }
      })
    },
    ajaxGetUserInfo (data) {
      api.getUserIndex(data).then((response) => {
        if (response.body.isSuccess) {
          this.userInfo = response.body.data
        }
      })
    },
    ajaxGetRechargeAdFeeDiscount () {
      api.getRechargeAdFeeDiscount().then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
          this.adFeeList = result.data
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';
.light-title {
  font-size: .32rem;
  padding: .4rem 0 .2rem .22rem;
}
</style>



